import React, { useEffect, useRef, useState } from "react";
import { gsap } from "gsap";

const FrameSequenceAnimation = ({ 
  images, 
  baseImage, 
  speed = 0.08, //    (   )
  className = "",
  imageClassName = ""
}) => {
  const containerRef = useRef(null);
  const imagesRef = useRef([]);
  const animationRef = useRef(null);
  const timeoutRef = useRef(null);
  const currentFrameRef = useRef(0);
  const isAnimatingRef = useRef(false);
  const directionRef = useRef(null); // 'forward'  'reverse'
  const [isMobile, setIsMobile] = useState(false);

  // ,      
  useEffect(() => {
    const checkIsMobile = () => {
      setIsMobile(window.innerWidth < 1200);
    };

    checkIsMobile();
    window.addEventListener('resize', checkIsMobile);

    return () => {
      window.removeEventListener('resize', checkIsMobile);
    };
  }, []);

  useEffect(() => {
    if (!containerRef.current || images.length === 0) return;

    const imageElements = imagesRef.current.filter(Boolean);

    if (imageElements.length === 0) return;

    //         
    if (isMobile) {
      const lastFrameIndex = imageElements.length - 1;
      imageElements.forEach((img, index) => {
        if (img) {
          if (index === lastFrameIndex) {
            gsap.set(img, {
              opacity: 1,
              display: "block",
              pointerEvents: "none",
              zIndex: 1
            });
          } else {
            gsap.set(img, {
              opacity: 0,
              display: "none",
              pointerEvents: "none"
            });
          }
        }
      });
      currentFrameRef.current = lastFrameIndex;
    } else {
      //   -    
      imageElements.forEach((img) => {
        if (img) {
          gsap.set(img, {
            opacity: 0,
            display: "none",
            pointerEvents: "none"
          });
        }
      });
      currentFrameRef.current = 0;
    }

    return () => {
      //   
      if (timeoutRef.current) {
        clearTimeout(timeoutRef.current);
        timeoutRef.current = null;
      }
      imageElements.forEach((img) => {
        if (img) {
          gsap.killTweensOf(img);
        }
      });
    };
  }, [images, isMobile]);

  const showFrame = (frameIndex, duration = 0.15) => {
    const imageElements = imagesRef.current.filter(Boolean);
    const totalFrames = imageElements.length;
    const targetFrame = Math.max(0, Math.min(frameIndex, totalFrames - 1));

    //  crossfade  
    imageElements.forEach((img, index) => {
      if (img) {
        if (index === targetFrame) {
          //      
          gsap.to(img, {
            opacity: 1,
            display: "block",
            duration: duration,
            ease: "power2.out",
            zIndex: 1
          });
        } else {
          //      
          gsap.to(img, {
            opacity: 0,
            duration: duration,
            ease: "power2.in",
            onComplete: () => {
              if (img.style.opacity === "0") {
                gsap.set(img, { display: "none" });
              }
            },
            zIndex: 0
          });
        }
      }
    });

    currentFrameRef.current = targetFrame;
  };

  const stopAnimation = () => {
    if (timeoutRef.current) {
      clearTimeout(timeoutRef.current);
      timeoutRef.current = null;
    }
    isAnimatingRef.current = false;
    directionRef.current = null;
  };

  const playForward = () => {
    const imageElements = imagesRef.current.filter(Boolean);
    const totalFrames = imageElements.length;

    //     ,   
    if (currentFrameRef.current >= totalFrames - 1) {
      isAnimatingRef.current = false;
      return;
    }

    //   
    stopAnimation();
    isAnimatingRef.current = true;
    directionRef.current = 'forward';

    //     (   )
    if (currentFrameRef.current === 0) {
      showFrame(0, speed * 1.5);
    }

    const animate = () => {
      if (!isAnimatingRef.current || directionRef.current !== 'forward') {
        return;
      }

      if (currentFrameRef.current < totalFrames - 1) {
        currentFrameRef.current++;
        showFrame(currentFrameRef.current, speed * 1.5);
        
        //      
        timeoutRef.current = setTimeout(() => {
          animate();
        }, speed * 1000 + 20); //    
      } else {
        //    -     
        isAnimatingRef.current = false;
        directionRef.current = null;
      }
    };

    //        
    timeoutRef.current = setTimeout(() => {
      animate();
    }, speed * 1000 + 50);
  };

  const playReverse = () => {
    const imageElements = imagesRef.current.filter(Boolean);
    const totalFrames = imageElements.length;

    //   
    stopAnimation();
    isAnimatingRef.current = true;
    directionRef.current = 'reverse';

    const animate = () => {
      if (!isAnimatingRef.current || directionRef.current !== 'reverse') {
        return;
      }

      if (currentFrameRef.current > 0) {
        currentFrameRef.current--;
        showFrame(currentFrameRef.current, speed * 1.5);
        
        //      
        timeoutRef.current = setTimeout(() => {
          animate();
        }, speed * 1000 + 20); //    
      } else {
        //    -    
        const imageElements = imagesRef.current.filter(Boolean);
        imageElements.forEach((img) => {
          if (img) {
            gsap.to(img, {
              opacity: 0,
              duration: speed * 1.5,
              ease: "power2.in",
              onComplete: () => {
                gsap.set(img, { display: "none", zIndex: 0 });
              }
            });
          }
        });
        currentFrameRef.current = 0;
        isAnimatingRef.current = false;
        directionRef.current = null;
      }
    };

    animate();
  };

  const handleMouseEnter = () => {
    //       
    if (isMobile) return;

    if (!containerRef.current || images.length === 0) return;

    const imageElements = imagesRef.current.filter(Boolean);
    if (imageElements.length === 0) return;

    //    ,  
    if (directionRef.current === 'reverse') {
      stopAnimation();
    }

    //        
    playForward();
  };

  const handleMouseLeave = () => {
    //       
    if (isMobile) return;

    //    ,  
    if (directionRef.current === 'forward') {
      stopAnimation();
    }

    //          
    playReverse();
  };

  return (
    <div 
      ref={containerRef} 
      className={`frame-sequence-container ${className}`}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
      style={{ cursor: 'pointer' }}
    >
      {baseImage && (
        <div className="frame-sequence-phone-wrapper">
          <img 
            src={baseImage} 
            alt="ChooseRight Phone" 
            className={`frame-sequence-base ${imageClassName}`}
          />
        </div>
      )}
      <div 
        ref={animationRef} 
        className="frame-sequence-animation"
      >
        {images.map((img, index) => (
          <img
            key={index}
            ref={(el) => (imagesRef.current[index] = el)}
            src={img}
            alt={`Yety Frame ${index + 1}`}
            className={`frame-sequence-frame ${imageClassName}`}
          />
        ))}
      </div>
    </div>
  );
};

export default FrameSequenceAnimation;
