import { useEffect, useRef } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

//  
gsap.registerPlugin(ScrollTrigger);

/**
 * ,    
 */
const isMobileDevice = () => {
  return window.innerWidth <= 768 || /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
};

/**
 * Hook for scroll-based run animation (   )
 * @param {React.RefObject} containerRef - Ref to the .main_run container
 * @param {React.RefObject} imageRef - Ref to the img element inside
 * @param {string} start - ScrollTrigger start position (default: "top bottom")
 * @param {string} end - ScrollTrigger end position (default: "+=300%" -     )
 */
export const useScrollRunAnimation = (
  containerRef,
  imageRef,
  start = "top bottom",
  end = "+=200%"
) => {
  const animationRef = useRef(null);

  useEffect(() => {
    //     
    if (!isMobileDevice()) {
      return;
    }

    if (!containerRef.current || !imageRef.current) {
      return;
    }

    const container = containerRef.current;
    const image = imageRef.current;

    //  CSS   
    image.style.animation = "none";

    //   
    gsap.set(image, {
      x: "33%",
    });

    //     
    if (animationRef.current) {
      animationRef.current.kill();
      animationRef.current = null;
    }

    //    ScrollTrigger
    const animation = gsap.to(image, {
      x: "-100%",
      ease: "none",
      scrollTrigger: {
        trigger: container,
        start: start,
        end: end,
        scrub: true, //    
        invalidateOnRefresh: true,
        markers: false, //  true  
      },
    });

    //    
    animationRef.current = animation;

    return () => {
      if (animationRef.current) {
        animationRef.current.kill();
        animationRef.current = null;
      }
      //  ScrollTrigger
      ScrollTrigger.getAll().forEach((trigger) => {
        if (trigger.vars && trigger.vars.trigger === container) {
          trigger.kill();
        }
      });
    };
  }, [containerRef, imageRef, start, end]);

  return animationRef;
};
